@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@600;700;800&display=swap);
* {
    margin: 0;
    padding: 0;
}

:root {
    font-size: 62.5%;
}

body {
    height: 100vh;
    
    color: rgb(255, 255, 255);

    background: #ebf2f5;
    
}

/* the others don't use the default font of the body */
/* / represents line-height */
body, input, button, textarea {
    font: 400 1.8rem/1 'Nunito', sans-serif;
}

@-webkit-keyframes up {
    from {
        opacity: 0;
        transform: translateY(16px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes up {
    from {
        opacity: 0;
        transform: translateY(16px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.animate-up {
    -webkit-animation-name: up;
            animation-name: up;
    -webkit-animation-duration: 300ms;
            animation-duration: 300ms;
    
    /* stops on 'to'; hold the style and not back to 'from' */
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}

/* the style order try to follow the same html order */

/* mobile first */
#page-landing {
    background: linear-gradient(329.54deg, #014065 0%, #002c46 100%);

    text-align: center;
    min-height: 100vh;
    /* min-width: 439px; */

    display: flex;
}

#container {
    margin: auto;

    width: min(90%, 112rem);
}

.location, h1, section p {
    height: 16vh
}

.logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding-bottom: 60px;

    -webkit-animation-delay: 50ms;

            animation-delay: 50ms;
}

.logo p {
    padding-left: 10px;
    font-size: 24px;
    font-weight: 800;
}

.location {
    -webkit-animation-delay: 100ms;
            animation-delay: 100ms;
}

.main h1 {
    -webkit-animation-delay: 150ms;
            animation-delay: 150ms;

    /* clamp(min, ideal, max) */
    font-size: clamp(4rem, 8vw, 8.4rem);
}

section p {
    -webkit-animation-delay: 200ms;
            animation-delay: 200ms;

    padding-top: 140px;
}

.visit {
    width: 8rem;
    height: 8rem;
    background: rgb(247, 150, 51);

    border: none;
    border-radius: 3rem;

    display: flex;
    align-items: center;
    justify-content: center;

    margin: auto;

    transition: background .2s;

    -webkit-animation-delay: 250ms;

            animation-delay: 250ms;
}

.visit:hover {
    background: rgb(110, 193, 228); 
}

/* desktop */
@media(min-width:760px) {
    #container {
        background: url(/tere-agasalhado-web/static/media/landing.db34ba2f.png) 
            no-repeat 80% / clamp(40rem, 54vw, 76rem);
    }

    header {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .location, h1, section p {
        height: auto;
        height: initial;
        text-align: left;
        text-align: initial;
    }

    .logo {
        padding-bottom: 0;
    }

    .location {
        text-align: right;
        font-size: 2.4rem;
        line-height: 1.5;
    }

    main h1 {
        font-weight: bold;
        line-height: .88;

        margin: clamp(10%, 9vh, 12%) 0 4rem;

        width: min(300px, 80%);
    }

    section {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    section p {
        font-size: 2.4rem;
        line-height: 1.5;
        width: clamp(20rem, 20vw, 30rem);

        padding-top: 0;
    }

    .visit {
        margin: 0;
        margin: initial;
    }
}

#page-map {
    width: 100vw;
    height: 100vh;

    display: flex;
    flex-direction: column;

    position: relative;
}

#page-map aside {
    padding: 10px;
    background: linear-gradient(329.54deg, #014065 0%, #002c46 100%);

    display: flex;
    flex-direction: column;
}

#page-map aside header {
    align-items: center;
    
    display: flex;
    flex-direction: column;
}

#page-map aside h2 {
    font-size: 35px;
    font-weight: 700;
    line-height: 32px;
    margin-top: 25px;
    text-align: center;
}

#page-map aside p {
    line-height: 28px;
    margin-top: 24px;
    text-align: center;
}

#page-map aside footer {
    line-height: 24px;
    margin-top: 24px;
    text-align: center;

    display: flex;
    flex-direction: column;
}

#page-map aside footer strong {
    font-weight: 800;
}

#page-map .leaflet-container {
    z-index: 5;
}

#page-map .create-collect-point {
    position: absolute;
    right: 40px;
    bottom: 40px;

    z-index: 10;

    width: 64px;
    height: 64px;
    background: rgb(247, 150, 51);
    border-radius: 20px;

    display: flex;
    justify-content: center;
    align-items: center;

    transition: background-color 0.2s;
}

#page-map .create-collect-point:hover {
    background: rgb(110, 193, 228); 
}

#page-map .map-popup .leaflet-popup-content-wrapper {
    background: rgba(255, 255, 255, .8);
    border-radius: 20px;
    /* box-shadow: none; */
}

#page-map .map-popup .leaflet-popup-content {
    color: #014065;
    font-size: 20px;
    font-weight: bold;
    margin: 8px 12px;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

#page-map .map-popup .leaflet-popup-content a {
    width: 40px;
    height: 40px;
    background: rgb(110, 193, 228);
    box-shadow: 17.2868px 27.6589px 41.4884px rgba(23, 142, 166, .16);
    border-radius: 12px;

    display: flex;
    justify-content: center;
    align-items: center;
}

#page-map .map-popup .leaflet-popup-tip-container {
    display: none;
}

/* desktop */
@media(min-width:760px) {
    #page-map {
        flex-direction: row;
    }
    
    #page-map aside {
        padding: 70px;
        width: 30%;

        align-items: flex-start;
        justify-content: space-between;
    }
    
    #page-map aside header {
        align-items: flex-start;
        
    }
    
    #page-map aside h2 {
        margin-top: 70px;
        text-align: start;
    }
    
    #page-map aside p {
        margin-top: 70px;
        text-align: start;
    }
    
    #page-map aside footer {
        margin-top: 0;
    }
}

#page-create-collect-point {
  display: flex;
}

#page-create-collect-point main {
  flex: 1 1;
}

form.create-collect-point-form {
  width: 700px;
  margin: 64px auto;

  background: #FFFFFF;
  border: 1px solid #D3E2E5;
  border-radius: 20px;

  box-shadow: 1px 2px 12px 1px rgba(0, 0, 0, .7);

  padding: 64px 80px;

  overflow: hidden;
}

form.create-collect-point-form .leaflet-container {
  margin-bottom: 40px;
  border: 1px solid #D3E2E5;
  border-radius: 20px;
}

form.create-collect-point-form fieldset {
  border: 0;
}

form.create-collect-point-form fieldset + fieldset {
  margin-top: 80px;
}

form.create-collect-point-form fieldset legend {
  width: 100%;

  font-size: 32px;
  line-height: 34px;
  /* color: #5C8599; */
  color: #002c46;
  font-weight: 700;

  border-bottom: 1px solid #D3E2E5;
  margin-bottom: 40px;
  padding-bottom: 24px;
}

form.create-collect-point-form .input-block + .input-block {
  margin-top: 24px;
}

form.create-collect-point-form .input-block label {
  display: flex;
  /* color: #8FA7B3; */
  color: #014065;
  margin-bottom: 8px;
  line-height: 24px;
}

form.create-collect-point-form .input-block label span {
  font-size: 14px;
  /* color: #8FA7B3; */
  color: #0089A5;
  margin-left: 24px;
  line-height: 24px;
}

form.create-collect-point-form .input-block input,
form.create-collect-point-form .input-block textarea {
  width: 100%;
  background: #F5F8FA;
  border: 1px solid #D3E2E5;
  border-radius: 20px;
  outline: none;
  /* color: #5C8599; */
  color: rgba(0, 0, 0, .8);
}

form.create-collect-point-form .input-block input {
  height: 64px;
  padding: 0 16px;
}

form.create-collect-point-form .input-block textarea {
  min-height: 120px;
  max-height: 240px;
  resize: vertical;
  padding: 16px;
  line-height: 28px;
}

form.create-collect-point-form .input-block .images-container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 16px;
}

form.create-collect-point-form .input-block .images-container img {
  width: 100%;
  height: 96px;
  object-fit: cover;
  border-radius: 20px;
}

form.create-collect-point-form .input-block .images-container .new-image {
  height: 96px;
  background: #F5F8FA;
  border: 1px dashed #96D2F0;
  border-radius: 20px;
  cursor: pointer;

  display: flex;
  justify-content: center;
  align-items: center;
}

form.create-collect-point-form .input-block input[type=file] {
  display: none;
}

form.create-collect-point-form .input-block .button-select {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

form.create-collect-point-form .input-block .button-select button {
  height: 64px;
  background: #F5F8FA;
  border: 1px solid #D3E2E5;
  color: #376983;
  cursor: pointer;
}

form.create-collect-point-form .input-block .button-select button.active {
  background: #EDFFF6;
  border: 1px solid #A1E9C5;
  color: #25965d;
}

form.create-collect-point-form .input-block .button-select button:first-child {
  border-radius: 20px 0px 0px 20px;
}

form.create-collect-point-form .input-block .button-select button:last-child {
  border-radius: 0 20px 20px 0;
  border-left: 0;
}

form.create-collect-point-form button.confirm-button {
  margin-top: 64px;

  width: 100%;
  height: 64px;
  border: 0;
  cursor: pointer;
  background: rgb(110, 193, 228);
  border-radius: 20px;
  color: #FFFFFF;
  font-weight: 800;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: background-color 0.2s;
}

form.create-collect-point-form button.confirm-button svg {
  margin-right: 16px;
}

form.create-collect-point-form button.confirm-button:hover {
  background: rgb(247, 150, 51);
}

aside.app-sidebar {
    position: fixed;
    height: 90%;
    padding: 32px 24px;
    background: linear-gradient(329.54deg, #014065 0%, #002c46 100%);
  
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
  
aside.app-sidebar img {
    width: 48px;
}
  
aside.app-sidebar footer a,
aside.app-sidebar footer button {
    width: 48px;
    height: 48px;
    border: 0;
    background: rgb(247, 150, 51);
    
    border-radius: 16px;
    cursor: pointer;
    transition: background-color 0.2s;
  
    display: flex;
    justify-content: center;
    align-items: center;
}
  
aside.app-sidebar footer a:hover,
aside.app-sidebar footer button:hover {
    background: rgb(110, 193, 228);
}

#page-collect-point {
  display: flex;
  min-height: 100vh;
}

#page-collect-point main {
  flex: 1 1;
}

.collect-point-details {
  width: 700px;
  margin: 64px auto;

  background: #fff;
  border: 1px solid #D3E2E5;
  border-radius: 20px;
  
  box-shadow: 1px 2px 12px 1px rgba(0, 0, 0, .7);

  overflow: hidden;
}

.collect-point-details > img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.collect-point-details .images {
  display: grid;
  grid-template-columns: repeat(6 ,1fr);
  grid-column-gap: 16px;
  -webkit-column-gap: 16px;
          column-gap: 16px;

  margin: 16px 40px 0;
}

.collect-point-details .images button {
  border: 0;
  height: 88px;
  background: none;
  cursor: pointer;
  border-radius: 20px;
  overflow: hidden;
  outline: none;

  opacity: 0.6;
}

.collect-point-details .images button.active {
  opacity: 1;
}

.collect-point-details .images button img {
  width: 100%;
  height: 88px;
  object-fit: cover;

}

.collect-point-details .collect-point-details-content {
  padding: 80px;
}

.collect-point-details .collect-point-details-content h1 {
  /* color: #4D6F80; */
  color: rgba(0, 0, 0, .8);
  color: #002c46;
  font-size: 54px;
  line-height: 54px;
  margin-bottom: 8px;
  margin-top: -4px;
}

.collect-point-details .collect-point-details-content p {
  line-height: 28px;
  /* color: #5C8599; */
  color: rgba(0, 0, 0, .8);
  margin-top: 24px;
}

.collect-point-details .collect-point-details-content .map-container {
  margin-top: 64px;
  background: #E6F7FB;
  border: 1px solid #B3DAE2;
  border-radius: 20px;
}

.collect-point-details .collect-point-details-content .map-container footer {
  padding: 20px 0;
  text-align: center;
}

.collect-point-details .collect-point-details-content .map-container footer a {
  line-height: 24px;
  color: #0089A5;
  text-decoration: none;
}

.collect-point-details .collect-point-details-content .map-container .leaflet-container {
  border-bottom: 1px solid #DDE3F0;
  border-radius: 20px;
}

.collect-point-details .collect-point-details-content hr {
  width: 100%;
  height: 1px;
  border: 0;
  /* background: #D3E2E6; */
  background: rgb(49, 48, 48);
  margin: 64px 0;
  box-shadow: 4px 2px 6px 1.5px rgba(0, 0, 0, .7);
}

.collect-point-details .collect-point-details-content h2 {
  font-size: 36px;
  line-height: 46px;
  /* color: #4D6F80; */
  color: #014065;
}

.collect-point-details .collect-point-details-content .open-details {
  margin-top: 24px;

  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
}

.collect-point-details .collect-point-details-content .open-details div {
  padding: 32px 24px;
  border-radius: 20px;
  line-height: 28px;
}

.collect-point-details .collect-point-details-content .open-details div svg {
  display: block;
  margin-bottom: 20px;
}

.collect-point-details .collect-point-details-content .open-details div.hour {
  background: linear-gradient(149.97deg, #E6F7FB 8.13%, #FFFFFF 92.67%);
  border: 1px solid #B3DAE2;
  /* color: #5C8599; */
  color: #366e8a;
}

.collect-point-details .collect-point-details-content .open-details div.open-on-weekends {
  background: linear-gradient(154.16deg, #EDFFF6 7.85%, #FFFFFF 91.03%);
  border: 1px solid #A1E9C5;
  /* color: #37C77F; */
  color: #1d7e4c;
}

.collect-point-details .collect-point-details-content .open-details div.open-on-weekends.dont-open {
  background: linear-gradient(154.16deg, #fdf0f5 7.85%, #FFFFFF 91.03%);
  border: 1px solid #ffbcd4;
  color: #bd3f61;
}

.collect-point-details .collect-point-details-content button.contact-button {
  margin-top: 64px;

  width: 100%;
  height: 64px;
  border: 0;
  cursor: pointer;
  background: #3CDC8C;
  border-radius: 20px;
  color: #FFFFFF;
  font-weight: 800;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: background-color 0.2s;
}

.collect-point-details .collect-point-details-content button.contact-button svg {
  margin-right: 16px;
}

.collect-point-details .collect-point-details-content button.contact-button:hover {
  background: #36CF82;
}

